	module.exports = {"data":{"markdownRemark":{"html":"<p>从教育会堂开会回来，一路快速行驶，突然发现一件奇怪的事情：一路上结婚的彩车特别之多，车上挂着彩带，五颜六色，有的在车子前方还竖立着一个玩具娃娃，风光的很。有一辆是\"林肯\"牌的大型车，神气得不行。数了，共遇到32辆。如果根据概率计算，与我们同方向车流里也大致应该有相同的数目，那就是60多辆。如果根据马路的长度计算，那上海今天结婚的人大概就惊人了。不禁觉得不同凡响了。</p>\n<p>心里一直在想：\"今天是什么日子?为什么有这么多的人?\"中午在东湖饭店见到一位本系的毕业生，也说是今天晚上结婚。决定回来\"研究\"。回来一看日历，今天是阴历9月18日，阳历22日，又是星期六。三个逢双，大概是黄道吉日。所以大家均安排到今天。</p>\n<p>当下，似乎人们对日期、号码、时间等等更加重视，上心。日期最好是8月18日，或者9月18日，数字最好是带8的。过去对4这个号码大家嫉讳，有个地方的出租司机拿到带4的牌照，竟然嚎啕大哭。不过，现在又有新解：在歌谱中4唱\"FA\"，于是乎又心安理得了。这样的\"信仰\"，或者民俗最近特别兴盛，过去好像没有这么强烈。</p>\n<p>大概这也是多方面的原因，或者是物质主义在社会上和在人们心中的地位大大上升，人们从信仰抽象的理念转移到\"信仰\"不可知的数字;或者是在一个快速变动的社会中，人们对自己的命运的把握更加不确定，寄希望于一种寄托来保证自己在未知的变动中不致失落;或者是人们已经没有把握自己命运的勇气，宁可让命运随波逐流。</p>\n<p>讨个吉利的心情可以理解。但这意味着社会的进步乎?停滞乎?后退乎?</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0165.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0165.md","frontmatter":{"title":"10月22日 星期六"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0165.md absPath of file >>> MarkdownRemark"}}

